import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals((metric) => {
  const description = {
    TTFB: "[Time To First Byte] Sunucunun ilk bayt taraycya ne kadar srede gnderdii bilgisi.",
    FCP: "[First Contentful Paint] Kullancya ilk ierik ne zaman gsterildi.",
    FID: "[First Input Delay] Kullancnn etkileime getii andan taraycnn yant verdii ana kadar geen sre.",
    LCP: "[Largest Contentful Paint] En byk grsel ya da metnin ekranda grnmesi sresi.",
    CLS: "[Cumulative Layout Shift] Sayfa dzeninin kullancy rahatsz edecek ekilde kaymas.",
  };

  const thresholds = {
    TTFB: 200,      // ms
    FCP: 1800,      // ms
    FID: 100,       // ms
    LCP: 2500,      // ms
    CLS: 0.1,       // ms
  };

  // metrige gore degerler ve durum belirle
  const value = metric.name === 'CLS' ? metric.value.toFixed(3) : Math.round(metric.value);
  const isGood = metric.value < thresholds[metric.name];
  const status = isGood ? ' GOOD' : ' BAD';

  // console ciktim
  console.log(` Metrik: ${metric.name} - ${description[metric.name]}`);
  console.log(`   Deer: ${value}`);
  console.log(`   Eik: < ${thresholds[metric.name]}`);
  console.log(`   Durum: ${status}\n`);

});


